import React, { Component } from 'react'
import classes from './Drawer.module.css'
import Backdrop from '../../UI/Backdrop/Backdrop'

const links = [

    'Link 1', 'Link 2', 'Link 3'
]

class Drawer extends Component {

    renderLinks() {

        return links.map((link, index) => {

            return (
                <li key={index}>

                    <a href="#">{link}</a>
                </li>
            )

        })
    }

    render() {

        const cls = [classes.Drawer]

        if (!this.props.isOpen) {

            cls.push(classes.close)

        }

        return (

            <div>

                <nav
                    className={cls.join(' ')}
                >

                    <b>My pet projects</b>
                    <br />
                    <br />

                    <ul>
                        {/* {this.renderLinks()} */}

                        <a href="https://mmoresun.github.io/employees/">Employees Dashboard App</a>  (react)
                        <br />
                        <br />
                        <a href="https://mmoresun.github.io/sizl-com/index.html">Sizl.com - food delivery project</a> (html, css, js)
                        <br />
                        <br />                        
                        <a href="https://rolling-scopes-school.github.io/mmoresun-JSFE2022Q1/shelter/index.html">Project Shelter</a> (html, css, js)
                    </ul>


                </nav>


                {this.props.isOpen ? <Backdrop onClick={this.props.onClose} /> : null}

            </div>

        )
    }
}

export default Drawer